
.project-list {
  margin: 0px;
  padding: 0px;
  list-style: none;
}

.project-entry {
  margin: 1rem;
  padding: 1rem 0rem;
  position: relative;

  & > * {
    margin: 0.2rem 0.2rem 0.4rem;
  }

  &:not(:last-child) {
    padding-bottom: 1em;
    border-bottom: 1px solid $color-highlight;
  }
  
  &::before {
    content: '\275E';
    position: absolute;
    font-size: 2.6em;
    font-weight: 100;
    left: -1em;
    transition: all 0.3s ease;
  }

  &:hover::before {
    transform: translateY(-0.2em);
  }
}

.project-title {
  font-size: 1.8em;

}

.project-content {
  padding: 0em 0.4em;
}

.project-tags {
  list-style: none;
  margin: 0px;
  padding: 0px;
  font-size: 0.8em;

  & > li {
    display: inline;
    padding: 0.2em 0.4em 0.1em;
    border-radius: 4px;
    color: $color-font2;
    background: $color-tag;
  }
}

.project-description {
  margin: 0.6em 0em 1em;
}

